import { Icon, Typography } from '@mui/material';
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { NavLink } from "react-router-dom";
import React from 'react';


const Politique = () => {
    return (
        <div>
             <NavLink to="/register">
        <Icon>
          <ArrowBackIcon />
        </Icon>
        </NavLink>
        
      <Typography variant="h6" sx={{ textAlign: "center" }}>
    Politique de confidentialité de l'application web "La grâce parle"
        </Typography>
        
            <Typography sx={{textAlign:'justify',}} variant='subtitle2' >Notre application web s'engage à protéger la vie privée de nos membres. Cette politique de confidentialité décrit les informations que nous collectons auprès de vous lorsque vous utilisez l'application, comment nous utilisons ces informations et avec qui nous les partageons.</Typography>
            <Typography variant='h6'>Collecte d'informations</Typography>
              <Typography variant='subtitle2'>Lorsque vous utilisez l'application, nous collectons les informations que vous nous fournissez, y compris votre nom, votre prénom, l'instrument que vous jouer, votre partition, votre profession, votre numéro de téléphone, votre adresse e-mail et votre adresse de facturation si vous effectuez des paiements via l'application.</Typography>
            <Typography variant='h6'>Utilisation d'informations</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous utilisons les informations collectées pour vous fournir les services de l'application, y compris la lecture de morceaux, la présence via l'application, les paiements et les téléchargements de fichiers. Nous pouvons également utiliser vos informations pour vous contacter au sujet de l'application, vous informer des mises à jour ou des promotions, ou vous demander votre avis sur le service rendu.</Typography>
            <Typography variant='h6'>Partage d'informations</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous ne partageons pas vos informations personnelles avec des tiers, sauf si cela est nécessaire pour fournir le service que vous avez demandé. Nous pouvons également divulguer vos informations si nous sommes tenus de le faire par la loi ou si nous croyons de bonne foi que cela est nécessaire pour protéger nos droits ou la sécurité de nos utilisateurs.</Typography>
            <Typography variant='h6'>Sécurité d'informations</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous prenons des mesures de sécurité raisonnables pour protéger les informations que nous collectons contre la perte, l'utilisation abusive et l'accès non autorisé, la divulgation, l'altération et la destruction.</Typography>
            <Typography variant='h6'>Cookies</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous utilisons des cookies pour collecter des informations sur votre utilisation de l'application et améliorer votre expérience utilisateur. Les cookies sont des fichiers texte placés sur votre ordinateur qui collectent des informations standard d'identification d'Internet et des informations sur les habitudes des visiteurs. Vous pouvez choisir de refuser les cookies ou de recevoir une notification avant qu'un cookie ne soit enregistré sur votre ordinateur.</Typography>
            <Typography variant='h6'>Modification de la politique de confidentialité</Typography>
            <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous nous réservons le droit de modifier cette politique de confidentialité à tout moment. Les modifications prendront effet immédiatement après leur publication sur la plateforme.</Typography>
            <Typography variant='h6'>Contactez-nous</Typography>
            <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Si vous avez des questions ou des préoccupations concernant notre politique de confidentialité, veuillez nous écrire sur whatsApp.</Typography>
            <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Dernière mise à jour : 01/04/2023.</Typography>
            

      <br></br>
        </div>
    );
};

export default Politique;