            user.average <= "20 " && 
            


          <Typography variant='subtitle2' >Bravo ! Une note supérieure à 18 est une excellente performance. Vous avez fait preuve de persévérance, de détermination et d'un grand travail pour atteindre ce résultat. Continuez à vous concentrer sur vos objectifs et à travailler dur pour atteindre vos ambitions. Vous êtes sur la bonne voie pour réussir et vous pouvez être fier(e) de vous !</Typography>
            
