






import { Alert, Box, Button, Container, Icon, Paper, Snackbar, Stack, TextField, Typography } from '@mui/material';
import axios from 'axios';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import SimpleBottomNavigation from '../../components/BottomBar';
import Navbar from '../../components/Navbar'
import { setGetUser, updateProfil } from '../../features/user.reducers';
import CloseIcon from '@mui/icons-material/Close';
import { useNavigate } from 'react-router-dom';


const Profil = () => {
    const user = useSelector(state => state.user.user)
    const dispatch = useDispatch()
    const [nom,setnom]= React.useState(user.firstName)
    const [prenom,setprenom]= React.useState(user.lastName)
    const [tel ,setTel]=React.useState(user.tel)
    const [email,setEmail]= React.useState(user.email)
  const [updateForm, setUpadateForm] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [resToSignIn, setResToSignIn] = React.useState(false)
   const [succees, setsuccees] = React.useState("");
  const [loading, setLoading] = React.useState(false)
  const history = useNavigate()
     const [error,setError] = React.useState('')
         const EditProfilRequest = async () => {
        
 const res =  await axios
            .put(`${process.env.REACT_APP_URL_USER}/update/${user._id}`,
                
 
                {
                    firstName:nom,
                    tel: tel,
                    lastName: prenom,
                    email:email
 }


              
 )
   .then((res) => {
     console.log(res.data.message);
      setLoading(false)
          setOpen(true)
     setsuccees(res.data.message)
     setResToSignIn(true)
      //  setUpadateForm(false)
      setTimeout(() => {
          history("/home");
        }, 3000);
     axios
            .get(`${process.env.REACT_APP_URL_USER}/jwt`)
 })
   .catch(error => {
        setLoading(false)
          setOpen(true)
   setError(error.response.data.message)
     ;

            })
    const datas = await res.data;
    return datas;
 
  }
    const handleUpdate = (e) => {
        e.preventDefault(); 

                setLoading(false)
       
      EditProfilRequest().then((data) => {
  dispatch(updateProfil(data)) 
   
               
        })
   
    }

   const getUserWithVerifyToken = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

    useEffect(() => {
    getUserWithVerifyToken().then((data) => {
      dispatch(setGetUser(data.user)); 
      console.log(data);
    }).catch((error) => {
      setTimeout(() => {
        console.log(error);
          history("/login");
        }, 2000);
     
    }); 
  },[dispatch,history]);
   
    return (
        <div>
            <Navbar />
           
        
        <Typography
          variant='h5' sx={{ textAlign: "center" }}>
                Profil
        </Typography>
            <Typography variant='subtitle2' color='secondary'>NB : Si c’est la partition qui est mal écrit ou l’instrument que vous jouer, veuillez notifier aux admins qui supprimeront ce profil, alors vous pouvez créer un autre compte</Typography>
          {updateForm === false &&   <Stack spacing={3} direction='column'>
                <Paper elevation={3}>{user.firstName}</Paper>
                <Paper elevation={3}>{user.lastName}</Paper>
                <Paper elevation={3}>{user.instrument}</Paper>
                <Paper elevation={3}>{user.partition}</Paper>
                <Paper elevation={3}>{user.tel}</Paper>
                <Paper elevation={3}>{user.profession}</Paper>
                 <Button type='submit' sx={{marginTop:3,borderRadius:1}} variant='contained' onClick={()=>setUpadateForm(!updateForm)}>Modifier mon profil</Button>
            </Stack>}

            {
                updateForm &&   <Container component="main" maxWidth="xs">
       
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
        
                        <Box component="form" onSubmit={handleUpdate} noValidate sx={{ mt: 1 }}>
                            <Icon>
                                





                                    < CloseIcon  onClick={()=>setUpadateForm(!updateForm)} sx={{
                                position:'absolute' , right:'15%',top :'60%',cursor:'pointer',fontSize:'2rem'
                                   }} />
                </Icon>
           <TextField
                                    required 
                                      label='Nom'
                                      fullWidth
                                margin='normal' type={'text'} variant='outlined' placeholder='nom'  value={nom} onChange={(e) => setnom(e.target.value)} />
          <TextField
                                    required  fullWidth
                                      label='Prenom'
                                margin='normal' type={'text'} variant='outlined' placeholder='prenom'  value={prenom} onChange={(e) => setprenom(e.target.value)} />
          <TextField
                                    required  fullWidth
                                      label='Email'
                                margin='normal' type={'email'} variant='outlined' placeholder='email'  value={email} onChange={(e) => setEmail(e.target.value)} />
          <TextField
                                    required  fullWidth
                                      label='tel'
                                margin='normal' type={'text'} variant='outlined' placeholder='tel'  value={tel} onChange={(e) => setTel(e.target.value)} />
                            <Stack direction='row'>
                     
                  {loading ?  <Button
              type="submit"
              fullWidth
                    variant="contained"
                    disabled
              sx={{ mt: 3, mb: 2 }}
            >
          Loading...
            </Button> :   <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
           Mettre à jour
            </Button> 
                  }
                   
           </Stack>
                        
      
            
                        </Box>
                         
          
            </Box>
           
              {resToSignIn ? <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="success"
            sx={{ width: "100%" }}
          >
            {succees}
          </Alert>
        </Snackbar>
      </Stack>: <Stack spacing={2} sx={{ width: "100%" }}>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert
            onClose={handleClose}
            severity="error"
            sx={{ width: "100%" }}
          >
            
            {error}
          </Alert>
        </Snackbar>
      </Stack>}
       
      </Container>
            }

 <Typography sx={{textAlign:'justify',}} variant='subtitle2' >Notre application web s'engage à protéger la vie privée de nos membres. Cette politique de confidentialité décrit les informations que nous collectons auprès de vous lorsque vous utilisez l'application, comment nous utilisons ces informations et avec qui nous les partageons.</Typography>
            <Typography variant='h6'>Collecte d'informations</Typography>
              <Typography variant='subtitle2'>Lorsque vous utilisez l'application, nous collectons les informations que vous nous fournissez, y compris votre nom, votre prénom, l'instrument que vous jouer, votre partition, votre profession, votre numéro de téléphone, votre adresse e-mail et votre adresse de facturation si vous effectuez des paiements via l'application.</Typography>
            <Typography variant='h6'>Utilisation d'informations</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous utilisons les informations collectées pour vous fournir les services de l'application, y compris la lecture de morceaux, la présence via l'application, les paiements et les téléchargements de fichiers. Nous pouvons également utiliser vos informations pour vous contacter au sujet de l'application, vous informer des mises à jour ou des promotions, ou vous demander votre avis sur le service rendu.</Typography>
            <Typography variant='h6'>Partage d'informations</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous ne partageons pas vos informations personnelles avec des tiers, sauf si cela est nécessaire pour fournir le service que vous avez demandé. Nous pouvons également divulguer vos informations si nous sommes tenus de le faire par la loi ou si nous croyons de bonne foi que cela est nécessaire pour protéger nos droits ou la sécurité de nos utilisateurs.</Typography>
            <Typography variant='h6'>Sécurité d'informations</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous prenons des mesures de sécurité raisonnables pour protéger les informations que nous collectons contre la perte, l'utilisation abusive et l'accès non autorisé, la divulgation, l'altération et la destruction.</Typography>
            <Typography variant='h6'>Cookies</Typography>
              <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous utilisons des cookies pour collecter des informations sur votre utilisation de l'application et améliorer votre expérience utilisateur. Les cookies sont des fichiers texte placés sur votre ordinateur qui collectent des informations standard d'identification d'Internet et des informations sur les habitudes des visiteurs. Vous pouvez choisir de refuser les cookies ou de recevoir une notification avant qu'un cookie ne soit enregistré sur votre ordinateur.</Typography>
            <Typography variant='h6'>Modification de la politique de confidentialité</Typography>
            <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Nous nous réservons le droit de modifier cette politique de confidentialité à tout moment. Les modifications prendront effet immédiatement après leur publication sur la plateforme.</Typography>
            <Typography variant='h6'>Contactez-nous</Typography>
            <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Si vous avez des questions ou des préoccupations concernant notre politique de confidentialité, veuillez nous écrire sur whatsApp.</Typography>
            <Typography variant='subtitle2' sx={{textAlign:'justify',}}>Dernière mise à jour : 01/04/2023.</Typography>
        
            <SimpleBottomNavigation sx={{position: 'sticky', bottom:0 ,left:'0',right:"0",zIndex:"1000"}} />
        </div>
    );
};

export default Profil; 